import React from 'react';
import styled from 'styled-components';
import axios from 'axios';
import { useState } from 'react';
import { useForm } from 'react-hook-form';

const Wrapper = styled.div`
      width: 100%;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 50px;
      h2{
            color: rgb(68,12,115);

            @media screen and (max-width: 760px){
                  font-size: 1.1em;
            }
      }
`

const Form = styled.form`
      display: flex;
      flex-direction: column;
      width: 50%;
      margin: 15px 0;
      @media screen and (max-width: 960px){
            width: 90%;
      }
      input{
            width: 90%;
            align-self: center;
            margin: 20px 0;
            padding: 1% 3%;
            border: none;
            border-bottom: 1px solid rgb(68,12,115);

            &:focus{
                  outline: none;
                 
            }
      }

      textarea{
            width: 90%;
            padding: 2% 3%;
            align-self: center;
            height: 150px;
            resize: none;
            overflow-y: auto;
            margin: 20px 0;
            outline: none;
            border: 1px solid rgb(68,12,115);
      }

      span{
            font-size: 0.8em;
            color: tomato;
            align-self: center;
      }

      span.statusMessage{
            color: purple;
            margin: 20px 0;
      }
`

const Button = styled.button`
     width: 150px;
      height: 30px;
      background-color: white;
      color: rgb(68,12,115);
      border: 2px solid rgb(68,12,115);
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 30px;
      position: relative;
      overflow: hidden;
      align-self: center;
      &:hover {
      background: transparent;
      box-shadow: 0 0 10px 7px rgba(139, 16, 173, 0.5);

            &::before {
                  transform: translateX(100%);
            }
      }
      &::before {
                  position: absolute;
                  content: "";
                  top: 0;
                  left: 0;
                  width: 100%;
                  height: 100%;
                  background: linear-gradient(
                        120deg,
                        transparent,
                        rgb(139, 16, 173),
                        transparent
                  );
                  transform: translateX(-100%);
                  transition: 0.5s;
            }

`

const SendMessageForm = () => {

      const [status, setStatus] = useState(0);
      const [statusMessage, setStatusMessage] = useState('');
      const button = document.getElementById('sendMailButton');
      const { register, handleSubmit, watch, errors } = useForm();
      const onSubmit = data => {
            let { mail, name, message } = data;
            let messageFull = `Mail: ${mail}   Name: ${name}    Message: ${message}`;
            setStatus(1);
            axios.post(process.env.REACT_APP_MAIL_URL, {   // URL to server that handle mail sending
                  "message": messageFull
            }).then(res => {
                  console.log(res);
                  setStatus(0);
                  setStatusMessage('Message was send succesfully. ');
                  setTimeout(() => setStatusMessage(''), 1000 * 7);
            }).catch(err => {
                  console.log(err);
                  setStatus(0);
                  setStatusMessage('Error occured, try again later. ');
                  setTimeout(() => setStatusMessage(''), 1000 * 7);
            })
      };

      return (
            <Wrapper>
                  <h2>Contact with me by filling this form </h2>
                  <Form onSubmit={handleSubmit(onSubmit)}>
                        <input type="text" placeholder='Your name' name='name' ref={register({
                              required: true,
                        })} />
                        {errors.name && <span>This field is required</span>}
                        <input type="text" placeholder='Your email' name='mail' ref={register({
                              required: true,
                              pattern: {
                                    value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                                    message: "invalid email address"
                              }
                        })} />
                        {errors.mail && <span>This field is required</span>}
                        <textarea name='message' ref={register({ required: true })}></textarea>
                        {errors.message && <span>This field is required</span>}
                        {status === 0 ? <Button type='submit' onClick={handleSubmit(onSubmit)}>Send message</Button>
                              : <Button type='submit' disabled onClick={handleSubmit(onSubmit)}>Sending...</Button>}
                        {statusMessage !== '' ? <span className='statusMessage'>{statusMessage}</span> : null}
                  </Form>
            </Wrapper>
      );
}

export default SendMessageForm;